import axios from 'axios';

const API_BASE_URL = 'http://localhost:8000/api';

const apiClient = axios.create({
  baseURL: API_BASE_URL,
  timeout: 10000,
  headers: {
    'Content-Type': 'application/json',
  },
});

export const getHealthStats = async (location = null) => {
  try {
    const response = await apiClient.get('/health/reports/stats', {
      params: location ? { location } : {}
    });
    return response.data;
  } catch (error) {
    console.error('Failed to fetch health stats:', error);
    return {};
  }
};

export const getHealthReports = async (location = null, limit = 100) => {
  try {
    const response = await apiClient.get('/health/reports', {
      params: { location, limit }
    });
    return response.data;
  } catch (error) {
    console.error('Failed to fetch health reports:', error);
    return [];
  }
};

export const getWaterQualityReports = async (location = null) => {
  try {
    const response = await apiClient.get('/water/quality', {
      params: location ? { location } : {}
    });
    return response.data;
  } catch (error) {
    console.error('Failed to fetch water quality reports:', error);
    return [];
  }
};

export const getWaterQualityStats = async () => {
  try {
    const reports = await getWaterQualityReports();
    const totalSources = reports.length;
    const contaminatedSources = reports.filter(r => r.is_contaminated).length;
    const safeSources = totalSources - contaminatedSources;
    
    return {
      total_sources: totalSources,
      contaminated_sources: contaminatedSources,
      safe_sources: safeSources,
      contamination_rate: totalSources > 0 ? (contaminatedSources / totalSources * 100).toFixed(1) : 0
    };
  } catch (error) {
    console.error('Failed to calculate water quality stats:', error);
    return {};
  }
};

